$topbgColor: #f6f6f6;
$blueColor: #1736DC;
$pinkColor: #FB56BF;
$redColor: #8A1737;
$lightbrownColor: #bb5e00;
$contentColor: #4A4A4A;
$orgColor: #F58D23;
$greColor: #539E43;
$purColor: #6762A6;

* {
  font-family: 微軟正黑體;
  border: 1px solid black;
  letter-spacing: 1px; }

html,body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0; }
.clearfix {
  clear: both; }

.cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  .card {
    width: 30%;
    height: 100%;
    box-shadow: 0px 0px 5px rgba(black,0.5);
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
    transition: 0.5s;
    &:hover {
      transform: translateY(-8px);
      box-shadow: 5px 5px 10px rgba(black,0.5); }
    a {
      display: block;
      width: 100%;
      img {
        width: 100%;
        border-radius: 8px; } }
    h2 {
      color: $redColor;
      text-align: center; }
    p {
      padding: 5px; } } }
hr {
  margin-top: -10px;
  margin-bottom: 20px;
  max-width: 60px;
  border: 2px solid $blueColor;
  font-weight: normal; }
li {
  margin-bottom: 8px; }


.wrap {
  width: 1024px;
  margin: 0 auto;
  .topheader {
    background-color: $topbgColor;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    h1 {
      margin: 0;
      margin: 15px 15px 0px 15px;
      display: inline-block; }
    ul {
      list-style: none;
      margin: {}
      li {
        float: left;
        margin: 15px 15px 0px 15px;
        a {
          text-decoration: none;
          padding-bottom: 15px;
          &:hover {
            color: $redColor;
            border-bottom: 2px solid $redColor; } } } } }
  .header {
    background-image: url(../image/img-headerShape.png);
    background-position: center center;
    background-size: cover;
    padding: 150px;
    display: flex;
    justify-content: space-between;
    .selftext {
      .top {
        margin-bottom: 8px;
        width: 120px;
        height: 120px;
        border-radius: 50%;
        img {
          width: 100%;
          border-radius: 50%; } }
      .bottom {
        .name {
          font-family: MicrosoftJhengHeiRegular;
          font-size: 42px; }
        .enname {
          font-family: MicrosoftJhengHeiRegular;
          font-size: 14px; } } }
    .infself {
      color: white;
      font-size: 20px;
      font-weight: normal;
      i {
        margin-right: 5px;
        margin-bottom: 8px;
        font-size: 24px; } } }
  #skills {
    margin-bottom: 150px;
    h3 {
      color:  $blueColor;
      font-size: 24px;
      text-align: center; }
    .cards {
      .web {
        border-bottom: 5px solid $orgColor; }
      .js {
        border-bottom: 5px solid $greColor; }
      .helper {
        border-bottom: 5px solid $purColor; }
      img {
        padding: 50px;
        margin-left: 50px; } } }
  #works {
    width: 100%;
    margin-bottom: 150px;
    h3 {
      color:  $pinkColor;
      font-size: 24px;
      text-align: center; } }

  #experience {
    width: 100%;
    margin-bottom: 150px;
    h3 {
      color:  $pinkColor;
      font-size: 24px;
      text-align: center; }
    .cards {
      .card.ang {
        text-align: center;
        border: 1px solid $pinkColor;
        h3,span {
          color: $lightbrownColor; }
        p {
          color: $contentColor; } } } }
  #about {
    margin-bottom: 150px;
    h3 {
      color:  $pinkColor;
      font-size: 24px;
      text-align: center; }
    .content {
      .left {
        display: inline-block;
        width: 30%;
        padding: 30px;
        img {
          width: 100%; } }
      .right {
        display: inline-block;
        width: 60%;
        margin-left: 10px;
        vertical-align: top;
        padding-top: 50px;
        p {
          text-indent: 2em;
          font-size: 15px; } } } }
  .fotter {
    height: 80px;
    background-image: url(../image/img-Looper-bg.png);
    display: flex;
    justify-content: center;
    align-items: center;
    h3 {
      font-size: 16px; } } }
